import React from 'react'
import styled from 'styled-components'
import coffee from './coffee-23.gif';


function Resume() {
    return (
        <Container>
            <resume>

            <header>
                <left>
                    <h3>Sharanu belaki</h3>
                </left>
                <right>
                    <p>
                        sharanubelaki123@gmail.com
                    </p>
                    
                    <p>Pune</p>
                </right>
            </header>
            <about>
                <leftinfo>
                    <p>EDUCATION</p>
                </leftinfo>
                <rightinfo>
                    <top><p>Bachelor of Engineering (B.E), Information Technology</p></top>
                    <bottom><p>Parvatibai Genba Moze College Of Engineering 2019 - 2023</p></bottom>
                    <top><p>Senior Secondary (XII), Science</p></top>
                    <bottom><p>Genba Sopanrao Moze Yerawada(MAHARASHTRA BOARD board)Year of completion: 2019</p></bottom>
                    <top><p>Secondary (X)</p></top>
                    <bottom><p>Sarathi Madhyamik Vidhyalay (SSE board) Year of completion: 2017</p></bottom>
                </rightinfo>
                
                
                   
                

            </about>
            <aboutskills>
                <leftinfo>
                    <p>SKILLS</p>
                </leftinfo>
                <rightinfo>
                <img src="https://img.icons8.com/color/80/000000/html-5--v1.png" alt="i"/>
                <img src="https://img.icons8.com/color/80/000000/css3.png" alt="i" />
                    <img src="https://img.icons8.com/color/80/000000/javascript.png" alt="i" />
                    <img src="https://img.icons8.com/office/80/000000/react.png" alt="i"/>
                    <img src="https://img.icons8.com/color/80/000000/redux.png" alt="i"/>
                    <img src="https://img.icons8.com/color/80/000000/firebase.png" alt="i" />
                    
                </rightinfo>
            </aboutskills>
            </resume>
            <cofee>
            <top>
                     <p>Lets have a cup of Coffee</p>
                     <button>Grab It!</button>
                </top>
                <bottom>

                </bottom>
            </cofee>
        </Container>
    )
}

export default Resume
const Container = styled.div`
display: flex;
width: 100%;
height: 90.5vh;
resume{
    flex: 0.6;
    background: black;
color: white;
@media (max-width: 480px) {
        flex: 1;
  }
header{
    
    margin: 0 1rem 0 1rem;
    border-bottom: 0.5px solid white;
    border-top: 0.5px solid white;
    display: flex;
    align-items: center;
    justify-content: space-between;

left{
    h3{
        border-bottom: 3px solid cyan;
        @media (max-width: 480px) {
        font-size: 16px;
  }
    }
}
    right{
        p{
            margin-block: 0;
            display: flex;
            justify-content: flex-end;
            font-size: 14px;
        }
    }
}
about{
display: flex;


    leftinfo{
        flex: 0.3;
        
        p{
            font-size: 13.5px;
            @media (max-width: 480px) {
        font-size: 12px;
  }
        }
    }
    rightinfo{
        flex: 0.7;
        margin: 0 1rem 0 1rem;
display: flex;
justify-content: center;
flex-direction: column;


        
    }
    top{
        margin-top: 15px;
            margin-bottom: 0;
        p{
            
            font-weight: 700;
            display: flex;
            
            justify-content: flex-start;
            text-align: justify;
            margin-block: 0;
            @media (max-width: 480px) {
        margin-top: 0;
        margin-block: 0;
        font-size: 13px;
  }
        }
        @media (max-width: 480px) {
        margin-top: 0;
  }
    }
    bottom{
        
        p{
            font-weight: 100;
            display: flex;
            justify-content: flex-start;
            text-align: justify;
            margin-block: 0;
            @media (max-width: 480px) {
        font-size: 13px;
  }
        }
      
    }
    
}
aboutskills{
      display: flex;
margin-top: 2rem;
@media (max-width: 480px) {
        margin-top: 1rem;
  }
      leftinfo{
            flex: 0.3;
            @media (max-width: 480px) {
        font-size: 12px;
  }
      }
      rightinfo{
          flex: 0.7;
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
        img{
             margin: 0.5rem;
        height: 3rem;
        width: 3rem;
    }
    
      }
       
     
  }
}
cofee{
flex: 0.4;
display: flex;
flex-direction: column;
height: 100%;
@media (max-width: 480px) {
        display: none;
  }
top{
    flex: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: black;
    color: white;
    font-size: 1.9rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;

}
bottom{
    flex: 0.5;
background-image:  linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,1)),linear-gradient(to left, rgba(0,0,0,0), rgba(0,0,0,1)),url(${coffee});
background-repeat: no-repeat;
background-size: 100% 100%;
}
@media (max-width: 480px) {
        display: none;
  }
}
button{
    color: #53452c;
    background: #7f7d78;
    outline: none;
    border: none;
    padding: 0.5rem 2rem 0.5rem 2rem;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 800;
    cursor:pointer;
     background: #eee;
  box-shadow: 0 2px 2px -1px #A67B5B, 0 3px 3px 1px #A67B5B;
  :hover{
      box-shadow: 0 0 0 -0.5px #A67B5B, 0 0 0 0.5px #A67B5B;
  }
  
  
}



`;