import React from 'react'
import styled from 'styled-components'
import coffee from './coffee-23.gif';
import YouTubeIcon from '@mui/icons-material/YouTube';
import GitHubIcon from '@mui/icons-material/GitHub';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
function About() {
    return (
        <Container>
            <about>
                <h2>About</h2>
                <p>I am a web Developer, I have been working as a web developer since 2019.

Ability to build efficient front-end user interfaces in line with modern best practices. I am experienced in using JavaScript, as well as the React framework, to develop fully functioned applications.</p>
            </about>
            <cofee>
                <top>
                     <p>Lets have a cup of Coffee</p>
                     <button>Grab It!</button>
                </top>
                <bottom>

                </bottom>
                
               
            </cofee>
            <social>
                    <ul>
                        <li>
                            <a href="https://www.instagram.com/re_coderjs">
                            <InstagramIcon/>
                            </a>
                        </li>
                        <li>
                        <a href="#">
                            <YouTubeIcon/>
                            </a>
                        </li>
                        <li>
                        <a href="https://github.com/sharan-cell/">
                            <GitHubIcon/>
                            </a>
                        </li>
                        <li>
                        <a href="https://www.linkedin.com/in/sharanu-belaki-bb66021b1/">
                            <LinkedInIcon/>
                            </a>
                        </li>
                    </ul>
                </social>
        </Container>
    )
}

export default About
const Container = styled.div`
display: flex;
width: 100%;
height: 90.5vh;


 
about{
flex: 0.5;
background: black;
height: 100%;
width: 100%;

@media (max-width: 480px) {
        flex: 0.9;
  }

h2{
    color: white;
    animation-name: examform;
  animation-duration: 1s;
@keyframes examform {
  0% {
    transform: translateY(-20px);
    


}
  50% {
    transform: translateY(10px);

}
  100% {
    transform: translateY(0);}
} 
}
p{
    color: white;
    font-size: 15px;
    font-weight: 100;
    line-height: 1.6;
    animation-name: examform;
  animation-duration: 1s;
@keyframes examform {
  0% {
    transform: translateY(-20px);
    


}
  50% {
    transform: translateY(10px);

}
  100% {
    transform: translateY(0);}
} 
}
}

cofee{
flex: 0.5;
display: flex;
flex-direction: column;
height: 100%;
top{
    flex: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: black;
    color: white;
    font-size: 1.9rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;

}
bottom{
    flex: 0.5;
background-image:  linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,1)),linear-gradient(to left, rgba(0,0,0,0), rgba(0,0,0,1)),url(${coffee});
background-repeat: no-repeat;
background-size: 100% 100%;
}
@media (max-width: 480px) {
        display: none;
  }
}
social{
    display: none;
    background: purple;
    ul{
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        
    transform: translateX(-15px);
        li{
            margin: 1rem 0 1rem 0;
            cursor: pointer;
            svg{
                color: white;
                
                
            }
        }
            
        }
    @media (max-width: 480px) {
        flex: 0.1;
        display: flex;
        flex-direction: column;
        
        
  }
}
button{
    color: #53452c;
    background: #7f7d78;
    outline: none;
    border: none;
    padding: 0.5rem 2rem 0.5rem 2rem;
    border-radius: 6px;
    font-size: 1rem;
    font-weight: 800;
    cursor:pointer;
     background: #eee;
  box-shadow: 0 2px 2px -1px #A67B5B, 0 3px 3px 1px #A67B5B;
  :hover{
      box-shadow: 0 0 0 -0.5px #A67B5B, 0 0 0 0.5px #A67B5B;
  }
  
  
}
`;