import React from 'react'
// import './skills.css'
import styled from 'styled-components';
import {useNavigate } from 'react-router-dom';










function Skills() {
  const navigate = useNavigate();
  function handleclick(e){
    e.preventDefault();
    navigate('contactme'); 
    
}
    return (
      
        <Container>
            
            
            
            
            <info>
            <h2>
              <p>Hi, i'm Sharanu Belaki.</p>
              <p>I'am a web developer!</p>
              <button onClick={handleclick}>Contact me</button>
            </h2>
            </info>
            

          
        </Container>
        
    )
}

export default Skills
const Container = styled.div`

background: black;
flex: 0.5;

::root{
cursor: none;
    --cursorX: 50vw;
    --cursorY: 50vh;
}


height: 90.5vh;
@media (max-width: 480px) {
        height: 39.25vh;
  }

color: white;
h2{
    color: white;
    animation-name: examform;
  animation-duration: 1s;
  @keyframes examform {
  0% {
    transform: translateY(-20px);
    


}
  50% {
    transform: translateY(10px);

}
  100% {
    transform: translateY(0);}
} 
}

ul{
  
  width: 100%;
  margin-top: 0%;
  
  
    list-style: none;
    li{
      cursor: pointer;
     color: white;
    }
}
mode{
  color: #cfd0d1;
display: block;
width: 100%;
height: 100%;
position: absolute;
top: 0;

cursor: default;
background: radial-gradient(
    circle 10vmax at var(--cursorX) var(--cursorY),
    rgba(0,0,0,0) 0%,
    rgba(0,0,0,.5) 80%,
    rgba(0,0,0,.95) 100%
);
.switch{
  position: absolute;
  right: 0;
  bottom: 0;
}
@media (max-width: 480px) {
    display: none;
  }
}
p{
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
info{
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;

  justify-content: center;
  @media (max-width: 480px) {
        align-items: flex-start;
  }
}
`;